@mixin pad {
    @media(max-width:992px) {
        @content;
    }
}

@mixin mobile {
    @media(max-width:767px) {
        @content;
    }
}

@mixin mobile-md {
    @media(max-width:414px) {
        @content;
    }
}

@mixin mobile-sm {
    @media(max-width:330px) {
        @content;
    }
}